body {
  margin: 0;
  background: #ffffff;
  color: #000000;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.heading{
  background: #252525;
  color: #ffffff;
  font-family: monospace;
  letter-spacing: 1em;
  padding: 1% 1%;
  display: flex;
  flex-wrap: wrap;
}

.hrLine{
  border: none;
  outline: none;
}

.btns{
  text-align: end;
  padding: 1.5% 1%;
}

.btn{
  font-size: 115%;
  cursor: pointer;
  user-select: none;
  margin-left: 2%;
  color: #b6b2b2;
}

.btn:hover{
  font-weight: bold;
  color: #ffffff;
}